@import url(https://unpkg.com/bootstrap@4.1.0/dist/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


thead,
tbody {
  display: block;
}

thead,
tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}

tbody {
  height: 75vh;
  width: 100%;
  overflow-y: auto;
}
.title {
  width: 200px;
  margin: 40px auto;
}
.center {
  width: 250px;
  margin: 40px auto;
}
.container {
  box-sizing: border-box;
}
.row {
  justify-content: space-around;
  padding-bottom: 100px;
}
.input-letter {
  width: 220px;
}

.buttonGroupPartida {
  margin: 0 auto;
}

.btn-group-vertical {
  width: 100%;
}

Button {
  border-radius: 10px;
}

.resultado {
  margin: 0px auto 40px auto;
  width: 125px;
}

.resultado.victoria {
  color: chartreuse;
}

.resultado.derrota {
  color: red;
}

.error {
  font-size: 100%;
  color: red;
  margin-top: 10px;
}

.status {
  font-size: 100%;
  margin-top: 10px;
}

.status.ok {
  color: green;
}

.status.bad {
  color: red;
}

